<script lang="ts">import { ColumnIndex, graphColors } from '../../lib/consts';
function getVersions(data) {
    const versions = new Set();
    for (let i = 0; i < data.length; i++) {
        const match = data[i][ColumnIndex.Path].match(/\/(v\d)[^a-z0-9]/i);
        if (match) {
            versions.add(match[1]);
        }
    }
    return versions;
}
function build(data) {
    versions = getVersions(data);
    if (versions.size > 1) {
        genPlot(data);
        window === null || window === void 0 ? void 0 : window.dispatchEvent(new Event('resize'));
    }
}
function getLayout() {
    return {
        title: false,
        autosize: true,
        margin: { r: 35, l: 70, t: 10, b: 20, pad: 0 },
        hovermode: 'closest',
        plot_bgcolor: 'transparent',
        paper_bgcolor: 'transparent',
        height: 180,
        yaxis: {
            title: { text: 'Requests' },
            gridcolor: 'gray',
            showgrid: false,
            fixedrange: true,
        },
        xaxis: {
            visible: false,
        },
        dragmode: false,
    };
}
function pieChart(data) {
    const versionCount = {};
    for (let i = 0; i < data.length; i++) {
        const match = data[i][ColumnIndex.Path].match(/[^a-z0-9](v\d)[^a-z0-9]/i);
        if (!match) {
            continue;
        }
        const version = match[1];
        if (version in versionCount) {
            versionCount[version]++;
        }
        else {
            versionCount[version] = 1;
        }
    }
    const versions = Object.keys(versionCount);
    const count = Object.values(versionCount);
    return [
        {
            values: count,
            labels: versions,
            type: 'pie',
            marker: {
                colors: graphColors,
            },
        },
    ];
}
function getPlotData(data) {
    return {
        data: pieChart(data),
        layout: getLayout(),
        config: {
            responsive: true,
            showSendToCloud: false,
            displayModeBar: false,
        },
    };
}
function genPlot(data) {
    const plotData = getPlotData(data);
    //@ts-ignore
    new Plotly.newPlot(plotDiv, plotData.data, plotData.layout, plotData.config);
}
let versions;
let plotDiv;
$: if (plotDiv && data && endpointsRendered) {
    build(data);
}
export let data, endpointsRendered;
</script>

<div class="card" class:hidden={versions === undefined || versions.size <= 1}>
	<div class="card-title">Version</div>
	<div id="plotly">
		<div id="plotDiv" bind:this={plotDiv}>
			<!-- Plotly chart will be drawn inside this DIV -->
		</div>
	</div>
</div>

<style scoped>
	.card {
		margin: 2em 0 2em 0;
