		flex: 1;
	}
	.hidden {
		display: none;
	}
	#plotDiv {
		margin-right: 20px;
	}
	@media screen and (max-width: 1030px) {
		.card {
			width: auto;
			flex: 1;
			margin: 0 0 2em 0;
		}
	}
</style>
